import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams} from 'ionic-angular';
import {ScreenOrientation} from "@ionic-native/screen-orientation";

/**
 * Generated class for the InicioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html',
})
export class inicio {
  public backgroundImage = 'assets/img/fondo.jpg'; 
  public waze = 'https://www.waze.com/ul?q=Hawaii'; 
  public ico01 = 'assets/img/hawaii.png';
  public ico02 = 'assets/img/vuelos.png';
  public ico03 = 'assets/img/agenda.png';
  public ico04 = 'assets/img/compania.png';
  public ico05 = 'assets/img/fotos.png';
  public ico06 = 'assets/img/waze.png';
  

  constructor( public navCtrl: NavController, public navParams: NavParams,screenOrientation: ScreenOrientation) {
      // screenOrientation.lock(screenOrientation.ORIENTATIONS.PORTRAIT);
  }



  goToVuelos() {
    this.navCtrl.push('VuelosPage');
  }

 goToAgenda() {
    this.navCtrl.push('AgendaPage');
  }

 goToprincipal() {
    this.navCtrl.push('PrincipalPage');
  }

  goToCompania() {
    this.navCtrl.push('PaisesPage');
  }

   openurl(url: string) {
    window.open(url, '_system', 'location=no'); return false;
  }

}



